.search-form {
  position: relative;
  border-bottom: $border_default;

  &:hover .search-form__icon {
    fill: $color_seafoam_blue;
    opacity: 1;
  }

  &:active .search-form__icon,
  &:focus .search-form__icon {
    fill: $color_seafoam_blue;
    opacity: 0.3;
  }
}

.search-form__input {
  margin: 0;
  padding: 25px 50px 30px 76px;
  width: 100%;
  font: inherit;
  border: none;
  background-color: inherit;
}

.search-form__input::placeholder {
  font-family: $font_family_base;
  font-size: $font_size_main;
  line-height: $line_height_main;
  font-weight: $font_weight_bold;
  color: $text_color_default;
}

.search-form__icon {
  @include style-icon(18px, 18px);
  fill: $color_black;
  opacity: 0.2;
}

@media (min-width: $width_tablet) {
  .search-form {
    padding: 44px 40px;
    border-left: $border_default;
    border-bottom: none;
  }

  .search-form__input {
    display: none;
  }
}
