.copyright {
  padding: 20px 0 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  font-size: 19px;
  line-height: 19px;
  color: $text_color_default;
}

.copyright__icon {
  margin-left: 30px;
  width: 27px;
  height: 34px;
  fill: $color_seafoam_blue;

  &:hover {
    fill: $color_greeny_blue;
  }

  &:active,
  &:focus {
    fill: $color_greeny_blue;
    opacity: 0.3;
  }
}
